/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

/* Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Global Styles */
body {
  font-family: "Lato", sans-serif;
  background-color: #121212;
  color: #eaeaea;
  overflow-x: hidden;
}

/* Container */
.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Hero Section */
.hero {
  position: relative;
  height: 100vh;
  background-image: url('/public/main.jpg');
  background-size: cover;
  background-position: center;
  filter: brightness(0.6);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero-title {
  font-family: "Righteous", sans-serif;
  font-size: 3rem;
  color: white;
}

.hero-subtitle {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  color: white;
}

/* Text Styling */
p {
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  color: #eaeaea;
  text-align: left;
}

/* Headings */
h2 {
  font-family: "Righteous", sans-serif;
  font-size: 2.8rem;
  margin-bottom: 20px;
}

/* Section Styling */
section {
  margin: 40px 0;
}

/* ----------------------------------------------------------------------------------------------------------- */
/* About Section */
.about-section {
  padding: 80px 20px;
  background-color: #1a1a1a;
  text-align: center;
}

.about-title {
  font-family: "Righteous", sans-serif;
  font-size: 2.2rem;
  margin: 0;
}

.about-content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin: 20px 0;
}

.about-image {
  max-width: 220px;
  height: 270px;
  object-fit: cover;
  margin-right: 30px;
  margin-left: -20px;
  border-radius: 10px;
}

.about-text {
  width: 40%;
  max-width: 600px;
  line-height: 1.4;
  color: #ffffff;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 20px;
}

.about-space {
  height: 20px;
}

/* ----------------------------------------------------------------------------------------------------------- */
/* Portfolio Section */
.portfolio-section {
  padding: 80px 20px;
  background-color: #141414;
  text-align: center;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
}

.project-card {
  background-color: #232323;
  padding: 20px;
  border-radius: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
}

.project-card h3 {
  color: #1e90ff;
  font-size: 1.4rem;
  margin: 20px 0;
}

.project-card p {
  font-size: 1rem;
  color: #eaeaea;
  text-align: left;
}

.view-more-container {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.view-more-button {
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  background-color: #ef0000;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s;
}

.view-more-button:hover {
  background-color: #d40000;
}

/* ----------------------------------------------------------------------------------------------------------- */
/* Modal Styling */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.modal-content {
  background-color: #1a1a1a;
  padding: 40px;
  border-radius: 15px;
  max-width: 600px;
  width: 80%;
  text-align: center;
  color: #00ff1e;
  position: relative;
}

.modal-content img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 2rem;
  color: #ef0000;
  cursor: pointer;
  transition: color 0.3s ease;
}

.close-button:hover {
  color: #ff5252;
}

/* ----------------------------------------------------------------------------------------------------------- */
/* Skills Section */
.skills-section {
  padding: 80px 20px;
  background-color: #1a1a1a;
  text-align: center;
}

.skills-section h2 {
  font-family: "Righteous", sans-serif;
  font-size: 2.8rem;
  color: #ffffff;
  margin-bottom: 20px;
}

.skills-section ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.skills-section li {
  background-color: #232323;
  padding: 15px 25px;
  border-radius: 10px;
  font-size: 1.2rem;
  color: #eaeaea;
  transition: background-color 0.3s ease;
  display: flex;
  align-items: center;
}

.skill-icon {
  width: 40px;
  height: auto;
  margin-right: 10px;
}

/* ----------------------------------------------------------------------------------------------------------- */
/* Contact Section */
.contact-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #141414;
}

.contact-section h2 {
  font-family: "Righteous", sans-serif;
  font-size: 2.8rem;
  color: #fff;
  margin-bottom: 20px;
}

.contact-button {
  background-color: #ef0000;
  color: #fff;
  padding: 15px 30px;
  font-size: 1.2rem;
  border: none;
  border-radius: 50px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.contact-button:hover {
  background-color: #ff5252;
}

/* ----------------------------------------------------------------------------------------------------------- */
/* Footer */
.footer {
  background-color: #0d0d0d;
  color: #eaeaea;
  padding: 40px 20px;
  text-align: center;
  border-top: 2px solid #ef0000;
}

.footer p {
  font-size: 0.9rem;
  color: #b0b0b0;
  margin: 0;
  padding-bottom: 15px;
  font-family: "Arial", sans-serif;
  letter-spacing: 0.5px;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-top: 10px;
}

.social-icon {
  color: #1e90ff;
  font-size: 1.6rem;
  text-decoration: none;
  font-weight: bold;
  border-radius: 50%;
  padding: 10px;
  transition: color 0.3s ease, background-color 0.3s ease, transform 0.3s ease;
}

.social-icon:hover {
  color: #ffffff;
  background-color: #1e90ff;
  transform: scale(1.1);
}

/* ----------------------------------------------------------------------------------------------------------- */
/* Testimonials Section */
.testimonials-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #1a1a1a;
}

.testimonials-section h2 {
  font-family: "Righteous", sans-serif;
  font-size: 2.8rem;
  color: #ffffff;
  margin-bottom: 40px;
}

.testimonials-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.testimonial-card {
  background-color: #232323;
  padding: 20px;
  border-radius: 10px;
  max-width: 600px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.testimonial-feedback {
  font-size: 1.2rem;
  font-style: italic;
  color: #eaeaea;
  margin-bottom: 10px;
}

.testimonial-name {
  font-weight: bold;
  color: #ffffff;
}

.testimonial-position {
  color: #a1a1a1;
}
